@use "sass:math";

$columns: 12;


body {
  background-color: map-get($colors, main-bgc);
}


@media (max-width: 1280px) {
  body {
    font-size: 14px;
  }
  .container {
    max-width: 960px;
  }

}

@media (max-width: 1000px) {
  .container {
    max-width: 960px;
    margin: 0 1em;
  }

}